.table-cards {
  padding: 50px 40px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 46px 48px;

  @media (min-width: 488px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: 768px) {
    grid-template-columns: repeat(3, 1fr);
  }

  @media (min-width: 1024px) {
    grid-template-columns: repeat(4, 1fr);
  }
}
